import React from 'react';
// import {input} from '@material-ui/core';
import './Signin.css';
function Signin() {
  return <div>
      <div className="merodokan">
          <img src="data:image/png;base64, " alt="" />
          
          </div>
	  <div className="signin">
		  <h1>Create Account</h1>
		  <form action="">
			  <div className="signin_column">
                  <label>Your Name*</label>
                  <input required name='firstname' />
              </div>
			  <div className="signin_column">
                  <label>Enter valid email*</label>
                  <input required name='email' />
              </div>
			  <div className="signin_column">
                  <label>Password*</label>
                  <input type='password' required name='firstname' placeholder='atleast 6 character'/>
              </div>
			  <div className="signin_column">
                  <button>Continue</button>
              </div>
              {/* <div className="condition"> */}
              <p>By creating an account, you agree to meroDokan's<br /> Conditions of Use and Privacy Notice.</p>
             
			  <p>Already have an account:signIn</p> 
		  </form>
	  </div>
  </div>;
}

export default Signin;
<div className="signin_form">

</div>
